
/*  Be careful 5G Active     */
* {
  scrollbar-width: thin;
  scrollbar-color: rgba(231, 231, 231, 0) rgba(231, 231, 231, 0);
}
*::-webkit-scrollbar {
  width: 0px;
}
*::-webkit-scrollbar-track {
  background: rgba(231, 231, 231, 0);
}
*::-webkit-scrollbar-thumb {
  background-color: rgba(231, 231, 231, 0)rgb(231, 231, 231);
  border-radius: 0px;
  border: 0px solid rgba(231, 231, 231, 0);
}
/*  Be careful 5G Active     */

@font-face {
  font-family: RussoOne-Regular;
  src:
  local(RussoOne-Regular),
  url(./fonts/RussoOne-Regular.ttf) format("woff");
}

@font-face {
  font-family: Inter-Bold;
  src:
  local(Inter-Bold),
  url(./fonts/Inter-Bold.ttf) format("woff");
}

@font-face {
  font-family: Jura-Light;
  src:
  local(Jura-Light),
  url(./fonts/Jura-Light.ttf) format("woff");
}

.main{
  width: 100%;
  height: 100vh;
}

#main{
  height: 100vh;
  width: 100vw;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',sans-serif;
}

video{
  height: 100vh;
  width: 100vw;
  object-fit: cover;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#tx1{
  font-size: 40px;}


.plce1{
  max-width: 700px;
  margin-top: 40px;
  margin-left: 40px;
