#home{
  height: 100%;
  width: 100%;
  position: fixed;
  overflow-y: scroll;
  background-color: rgba(0, 0, 0, 0.555);
}
@media screen and (max-width: 740px) {#home{display: none}}

#home2{
  height: 100%;
  width: 100%;
  position: absolute;
  background-color: black;
}
@media screen and (min-width: 740px) {#home2{display: none}}

#logo{
  display: inline-block;
  height: 100px;
  width: 100px;
  margin-left: 20px;
  margin-right: 20px;
}

#home_top_bar{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 100px;
  width: 100vw;
  margin-top: 20px;
  background-color: rgba(137, 137, 137, 0.386);
  backdrop-filter: blur(40px);
  border-style: solid;
  border-width: 2px;
  border-color: transparent;
  border-bottom-color: white;
}

#main-name{
