#bt_test{
  height: 60px;
  width: 200px;
  padding-left: 10px;
  padding-right: 10px;
  border-width: 0px;
  border-style: solid;
  inset: 0;
  border-radius: 20px; 
  padding: 5px; 
  background:linear-gradient(45deg,red,blue); 
  -webkit-mask: 
     linear-gradient(#fff 0 0) content-box, 
     linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
          mask-composite: exclude;  
}

